import { Component, h, Host, Prop } from '@stencil/core';

export interface StatusBar {
    tooltip?: string;
    percentage: number;
    backgroundColor: string;
}

type Size = 'sm' | 'lg';

/**
 * @category Data Visualization
 * @description A status bar component for displaying progress, completion, or state information with customizable colors and labels.
 */
@Component({
    tag: 'atui-status-bar',
    shadow: false,
})
export class AtuiStatusBar {
    /**
     * List of segments that make up the status bar. Each segment implements the StatusBar interface.
     */
    @Prop() status_bar: StatusBar[] = [];

    /**
     * Height of the status bar
     */
    @Prop()
    size?: Size = 'sm';

    /**
     * Disables the tooltip when hovered
     */
    @Prop()
    disable_tooltip?: boolean;

    get statusBarClass(): string {
        return `${this.size === 'lg' ? 'h-[16px]' : 'h-8'} flex items-stretch rounded-sm`;
    }

    get segments(): any {
        return this.status_bar.map((segment) => (
            <atui-tooltip
                position="bottom"
                class="flex flex-grow items-stretch justify-center"
                disabled={this.disable_tooltip}
                style={{
                    flexBasis: segment.percentage.toString() + '%',
                }}
            >
                <div
                    class="flex flex-grow"
                    slot="tooltip-trigger"
                    style={{
                        background: segment.backgroundColor,
                    }}
                ></div>
                <span>{segment.tooltip}</span>
            </atui-tooltip>
        ));
    }

    render() {
        return (
            <Host class={this.statusBarClass}>
                <div class="flex flex-1 items-stretch justify-start overflow-visible">
                    {this.segments}
                </div>
            </Host>
        );
    }
}
